.js30-content .inbox {
  max-width: 400px;
  margin: 50px auto;
  border-radius: 5px;
  box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.1);
}
.js30-content .item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #444;
}
.js30-content .item:last-child {
  border-bottom: 0;
}
.js30-content input:checked + p {
  text-decoration: line-through;
}
.js30-content input[type=checkbox] {
  margin: 20px;
}
.js30-content p {
  margin: 0;
  padding: 20px;
  transition: background 0.2s;
  flex: 1;
  font-size: 20px;
  font-weight: 200;
}